<!-- 阅读内容 -->
<template>
  <div class="readContent" @click.stop="popup">
    <div class="book-title">
      <div class="book-text"></div>
    </div>
    <div class="popup-box" @click.stop="jzct" :class="[ishide ? 'hide' : '']">
      <div class="popup-top">
        <div class="top-nav">
          <van-nav-bar @click-left="onClickLeft" left-arrow fixed>
            <template #right>
              <van-icon
                name="smile-comment-o"
                size="18"
                :style="{ 'margin-right': '5px' }"
              />
              <van-icon
                name="label-o"
                size="18"
                @click="getId"
                :style="{ 'margin-left': '5px' }"
              />
            </template>
          </van-nav-bar>
        </div>
      </div>
      <div class="popup-font">
        <div class="font-box">
          <div class="book-index">{{ bookNmae }}</div>
          <div class="book-count">
            <van-icon class="book-left" @click="jian" name="arrow-left" />
            <van-slider
              class="slider"
              :max="maxValue"
              v-model="value"
              button-size="14px"
              @change="onChange"
            />
            <van-icon class="book-right" @click="jia" name="arrow" />
          </div>
        </div>
        <div class="font-sz">
          <div class="font-ml">
            <div class="ml-img" @click="tcleft">
              <img class="auto-img" src="../assets/bookml.png" alt="" />
            </div>
            目录
          </div>
          <div class="font-ml">
            <div class="ml-img">
              <img class="auto-img" src="../assets/szaa.png" alt="" />
            </div>
            设置
          </div>
          <div class="font-ml">
            <div class="ml-img">
              <img class="auto-img" src="../assets/yj.png" alt="" />
            </div>
            夜间
          </div>
          <div class="font-ml">
            <div class="ml-img">
              <img class="auto-img" src="../assets/gd.png" alt="" />
            </div>
            更多
          </div>
        </div>
      </div>
    </div>
    <van-popup
      v-model="isshowto"
      position="left"
      :style="{ height: '100%', width: '60%' }"
    >
      <div class="bookbox">
        <div class="bookbox-img">
          <div class="img-box">
            <img class="auto-img" :src="ContentBox.coverImage" alt="" />
          </div>
          <div class="bookbox-nt">
            <div class="bookbox-name">{{ ContentBox.author }}</div>
            <div class="bookbox-title">{{ ContentBox.title }}</div>
          </div>
        </div>
        <div class="bookbox-count">
          <div class="count">共{{ BookContent.length }}章</div>
          <div class="chapter-order">
            倒序
            <van-icon name="arrow-up" />
          </div>
        </div>
        <div class="cata-list">
          <div
            class="cata-box"
            v-for="(list, index) in CatalogData"
            :key="index"
            @click="gitZj(list, index)"
          >
            {{ list.title }}
            <div class="suo" :class="{ hide: !list.needPay }">
              <img class="auto-img" src="../assets/suo.png" alt="" />
            </div>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "BookContent",
  data() {
    return {
      ContentBox: [],
      BookContent: [],
      BookNewContent: [],
      bookId: "",
      CatalogData: [],
      newCataLogData: [],
      isIndex: 1,
      BookList: [],
      ListIndex: [],
      cataIndex: "",
      newSecId: null,
      gbContent: false,
      showp: false,
      isshowto: false,
      ishide: true,
      maxValue: "",
      value: 1,
      bookNmae: "",
    };
  },

  methods: {
    async gitZj(e, i) {
       this.isIndex = i;
      if (!e.needPay) {
      await this.axios.get(`https://apis.netstart.cn/yunyuedu/reader/book/content.json?source_uuid=${this.$route.params.id}&content_uuid=${e.secId}`).then(re => {
            this.BookContent = re.content;
            this.bookNmae = e.title;
            let btext = document.querySelector(".book-text");
            btext.innerHTML = this.BookContent;
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$toast("VIP章节无法阅读");
      }
    },

    //禁止点击穿透
    jzct() {
      event.stopPropagation();
    },

    getId() {
      this.$store.commit("BSC", this.bookId);
      // for (var i = 0; i < this.$store.state.bookSc.length; i++) {
      //   if (this.bookId === this.$store.state.bookSc[i]) {
      //     this.$store.state.bookSc.splice(i, 1);
      //   }
      // }
      console.log(this.$store.state.bookSc);
    },

    tcleft() {
      this.isshowto = true;
    },

    //弹出层
    popup() {
      if (this.ishide) {
        this.ishide = false;
      } else {
        this.ishide = true;
      }
    },

    //上下切换章节
    jia() {
      this.gbContent = true;
      this.getBookList();
    },
   async jian() {
     this.gbContent = true;
    await  this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=" +this.bookId).then(re => {
          this.BookList = re.catalog;
          for (var i = 1; i < this.BookList.length; i++) {
            if (i == parseInt(this.isIndex) - 1) {
              this.newCataLogData = this.BookList[i];
              this.newSecId = this.newCataLogData.secId;
              this.bookNmae = this.newCataLogData.title;
              this.isIndex = i;
              this.getNewSec();
              break;
            }
          }
          if (this.BookList[this.isIndex].needPay === true) {
            this.$toast("VIP章节无法进入");
            return;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },

    //进度条控制
    onChange(value) {
      this.getCatalogData(value);
    },

    //获取书籍list
   async getBookList() {
     await  this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=" +this.bookId).then(re=> {
          this.BookList = re.catalog;
          for (var i = 0; i < this.BookList.length; i++) {
            if (i == parseInt(this.isIndex) + 1) {
              this.newCataLogData = this.BookList[i];
              this.newSecId = this.newCataLogData.secId;
              this.isIndex = i;
              console.log(this.isIndex);
              this.bookNmae = this.newCataLogData.title;
              this.getNewSec();
              break;
            }
            if (this.BookList[i].needPay === true) {
              this.$toast("VIP章节无法进入");
              return;
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //获取新的书籍数据
    async getNewSec() {
     await  this.axios.get(`https://apis.netstart.cn/yunyuedu/reader/book/content.json?source_uuid=${this.$route.params.id}&content_uuid=${this.newSecId}`).then(re => {
          this.BookNewContent = re.content;
          let btext = document.querySelector(".book-text");
          let zl = document.createElement("div");
          zl.classList.add("book-text");
          if (this.gbContent) {
            btext.innerHTML = this.BookNewContent;
            this.gbContent = false;
          } else {
            zl.innerHTML = this.BookNewContent;
            btext.appendChild(zl);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    onscrollData() {
      let that = this;
      this.$nextTick(() => {
        // this.initScroll()
        window.onscroll = function () {
          //变量scrollTop是滚动条滚动时，距离顶部的距离
          var scrollTop =
            document.documentElement.scrollTop || document.body.scrollTop;
          //变量windowHeight是可视区的高度
          var windowHeight =
            document.documentElement.clientHeight || document.body.clientHeight;
          //变量scrollHeight是滚动条的总高度
          var scrollHeight =
            document.documentElement.scrollHeight || document.body.scrollHeight;
          //滚动条到底部的条件
          if (scrollTop + windowHeight == scrollHeight) {
            that.getBookList();
          }
        };
      });
    },

    //书籍内容获取
 async getBookContent() {
   await this.axios.get(`https://apis.netstart.cn/yunyuedu/reader/book/content.json?source_uuid=${this.$route.params.id}&content_uuid=${this.$route.params.sid}`).then(re => {
          this.BookContent = re.content;
          let btext = document.querySelector(".book-text");
          btext.innerHTML = this.BookContent;
        })
    },

    //初始化获取当前书籍数据
   async getCatalogData(v) {
     await this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid="+this.bookId).then(re => {
          this.CatalogData = re.catalog;
          console.log(re);
          this.ContentBox = re.book;
          for (var i = 1; i < this.CatalogData.length; i++) {
            if (this.isIndex === i) {
              this.bookNmae = this.CatalogData[v].title;
              if (this.CatalogData[v].needPay === true) {
                this.$toast("VIP章节无法进入");
              }
            }
          }
          this.maxValue = this.CatalogData.length;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  created() {
    this.bookId = this.$route.params.id;
    this.isIndex = this.$route.params.index;
    this.getBookContent();
    this.onscrollData();
    this.getCatalogData(this.value);
  },
};
</script>

<style lang="scss" scoped>
.book-text{
    font-size: 16px;
    line-height: 35px;
    text-indent: 1rem;
    background-color: #e5f1d6;
}
.bookbox {
  padding: 10px;
  .bookbox-img {
    display: flex;
    align-items: center;
    .img-box {
      width: 80px;
      height: 100px;
    }
    .bookbox-nt {
      margin-left: 15px;
      .bookbox-title {
        font-size: 12px;
        color: #555;
        padding: 10px 0;
      }
      .bookbox-name {
        font-size: 14px;
      }
    }
  }
  .bookbox-count {
    display: flex;
    .chapter-order {
      margin-left: auto;
    }
  }
  .cata-box {
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px dotted #666;
    .suo {
      margin-left: auto;
    }
  }
}

.font-sz {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 20px;
  text-align: center;
  .font-ml {
    color: #555;
    .ml-img {
      width: 20px;
    }
  }
}

.BookContent {
  background-color: #e0f0e6;
  position: relative;
}

.van-nav-bar .van-icon {
  color: #555;
}

 .van-progress {
  background-color: #fff;
  margin: 10px;
  color: #555;
}

.hide {
  transition: none;
  display: none;
}

.popup-box {
  // display: none;
  z-index: 3;
  .popup-font {
    position: fixed;
    bottom: 0;
    background-color: #fff;
    width: 100%;
    .book-index {
      padding: 15px;
      text-align: center;
    }
    .book-count {
      display: flex;
      align-items: center;
      padding: 10px;
      .book-right {
        margin: 0 15px;
      }
      .book-left {
        margin: 0 15px;
      }
      .slider {
        flex: 2;
      }
    }
  }
}

.book-text {
  width: calc(100% - 20px);
  padding: 10px;
}

::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 0px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 0px;
}
::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
  background: #fff;
}
::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(87, 175, 187, 0.1);
  border-radius: 10px;
  background: #ededed;
}
</style>
